import { HeaderType } from '@/lib/type'
import { cn } from '@/lib/utils'
import React from 'react'

const Header = ({ classname, children, variant }: HeaderType) => {
    const classVariant = {
        xl: "text-[70px]",
        md: "text-[50px]"
    }

  return (
    <div className={cn(classname, classVariant[variant])}>
        {children}
    </div>
  )
}

export default Header